.container {
  padding: 0 20px;
}

.title {
  position: relative;
  margin-top: 15vh;
  padding-top: 10px;
  padding-left: 25px;
  font-size: 25px;
  color: #ffffff;
  font-weight: 800;
  line-height: 1.122;
}

.title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 22%;
  height: 1px;
  background-color: #d8771c;
}

.featuresList {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  margin-top: 10vh;
  margin-bottom: 10vh;
  padding-left: 0;
  list-style: none;
}

.feature {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.feature:not(:last-child) {
  margin-right: 5%;
}

.value {
  padding-bottom: 5px;
  border-bottom: 1px solid #848484;
  font-size: 26px;
  color: #ffffff;
  font-weight: 800;
  line-height: 0.657;
}

.label {
  padding-top: 10px;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.09);
  font-weight: 800;
  line-height: 0.961;
}

@media only screen and (min-width: 768px) {
  .feature:not(:last-child) {
    margin-right: 25px;
  }

  .value {
    font-size: 39px;
  }

  .label {
    font-size: 27px;
  }
}

@media only screen and (min-width: 768px) {
  .featuresList {
    margin-left: 16%;
    margin-right: 16%;
  }

  .value {
    font-size: 70px;
  }

  .label {
    font-size: 48px;
  }
}
