.container {
  padding: 0 20px;
}

.title {
  position: relative;
  margin-top: 15vh;
  padding-top: 10px;
  padding-left: 25px;
  font-size: 24px;
  color: #ffffff;
  font-weight: 800;
  line-height: 1.122;
}

.title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 22%;
  height: 1px;
  background-color: #d8771c;
}

.serviceContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 30vh);
  margin-top: 10vh;
  padding-left: 0;
  list-style: none;
}

.seriveItem {
  margin-bottom: -2px;
  margin-right: -2px;
  padding: 20px 10px;
  border: 3px solid #fff;
}

.itemContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  padding: 10px;
  text-align: center;
  font-size: 14px;
  line-height: 16px;
  font-weight: 600;
}

.seriveItem:hover .itemContainer {
  color: #323232;
  background-color: #ffffff;
  transition: all 0.3s ease-in-out;
}

.icon {
  font-size: 50px;
  transition: all 0.3s ease-in-out;
}

.seriveItem:hover .icon {
  color: #d8771c;
}

.verticalLine {
  margin: 10px 0;
  background-color: rgb(220, 220, 220);
  width: 2px;
  height: 53px;
  transition: all 0.3s ease-in-out;
}

.seriveItem:hover .verticalLine {
  height: 0;
}

.smallText {
  margin-top: 10px;
  font-size: 8px;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  height: 0;
  width: 90px;
  margin-top: 10px;
  box-shadow: 0px 11px 18px 0px rgba(0, 1, 5, 0.28);
  background-color: rgb(107, 36, 64);
  border-radius: 40px;
  font-size: 9px;
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

.seriveItem:hover .button {
  height: 46px;
}

@media only screen and (min-width: 768px) {
  .serviceContainer {
    padding: 0 100px;
  }

  .itemContainer {
    font-size: 18px;
  }

  .icon {
    font-size: 65px;
  }

  .verticalLine {
    height: 75px;
    margin: 15px 0;
  }

  .button {
    margin-top: 30px;
    font-size: 12px;
  }

  .seriveItem:hover .button {
    height: 50px;
  }

  .smallText {
    font-size: 10px;
  }
}
