.container {
  padding: 0 20px;
}

.title {
  position: relative;
  margin-top: 15vh;
  padding-top: 10px;
  padding-left: 25px;
  font-size: 24px;
  color: #ffffff;
  font-weight: 800;
  line-height: 1.122;
}

.title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 22%;
  height: 1px;
  background-color: #d8771c;
}

.contactsContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  margin-top: 10vh;
  margin-bottom: 10vh;
  padding-left: 0;
  list-style: none;
}

.contactItem {
  display: flex;
  flex-direction: column;
}

.label {
  position: relative;
  margin-bottom: 15px;
  padding-bottom: 5px;
  font-weight: 600;
}

.value {
  font-weight: 800;
}

.label::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 40%;
  height: 3px;
  background-color: #6b245e;
  border-radius: 1px;
}

.formContainer {
  background-color: #454545;
  padding: 20px 30px 10px;
  box-shadow: 0px 0px 38px 0px rgba(0, 1, 5, 0.5);
}

.formTitle {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 21px;
  text-align: center;
  font-weight: 800;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input {
  margin-bottom: 5px;
  padding: 15px 25px;
  font-size: 10px;
  border-radius: 40px;
  box-shadow: 0px 11px 18px 0px rgba(0, 1, 5, 0.28);
  border: none;
}

.button {
  margin-top: 15px;
  padding: 17px 20px;
  width: 125px;
  box-shadow: 0px 11px 18px 0px rgba(0, 1, 5, 0.28);
  background-color: rgb(107, 36, 64);
  border-radius: 40px;
  border: none;
  font-size: 10px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
}

@media only screen and (min-width: 768px) {
  .value {
    font-size: 22px;
  }

  .label {
    font-size: 24px;
  }

  .formTitle {
    font-size: 30px;
  }

  .input {
    padding: 25px 30px;
    font-size: 15px;
    width: 70%;
  }

  .button {
    font-size: 14px;
    width: 179px;
    height: 72px;
  }
}
