.container {
  position: fixed;
  right: -100%;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
  height: 100%;
  padding: 30px;
  box-sizing: border-box;
  background: #ffffff;
  z-index: 100;
  transition: all 0.3s ease-out;
}

.open {
  right: 0;
}

.openButton {
  position: absolute;
  top: 30px;
  right: 30px;
  font-size: 35px;
}

.closeButton {
  position: absolute;
  top: 30px;
  right: 30px;
  font-size: 35px;
  color: #323232;
}

.linkList {
  margin-top: -100%;
  margin-bottom: 0;
  padding-left: 0px;
  list-style: none;
}

.link {
  display: block;
  font-size: 21px;
  padding: 5px 0;
  color: #323232;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}

.link.active {
  color: #6b2440;
  border-bottom: 1px solid #6b2440;
}

@media only screen and (min-width: 768px) {
  .container {
    display: none;
  }

  .openButton {
    display: none;
  }
}
