.container {
  position: relative;
  background-color: #333333;
  box-shadow: 0px 0px 68px 0px rgba(0, 1, 5, 0.35);
}

.container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 12px;
  background-color: #d8771c;
}

.logo {
  margin-top: 60px;
  padding-left: 10px;
}

.pageNumber {
  margin-top: 90px;
  padding-left: 10px;
  font-size: 22px;
  color: #ffffff;
  font-weight: 800;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.pageSymbol {
  width: 10px;
  height: 10px;
  margin: 0 auto 35px;
  background-color: rgb(255, 255, 255);
  border-radius: 50%;
}

.pageSymbol.active {
  background-color: #6b245e;
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 43px;
  height: 43px;
  margin: 0 auto;
  border-radius: 50%;
  background-color: #ffffff;
  box-shadow: 0px 3px 5px 0px rgba(0, 1, 5, 0.34);
}

.icon {
  font-size: 22px;
  color: #6b245e;
}

@media only screen and (min-width: 768px) {
  .pageNumber {
    font-size: 40px;
    text-align: right;
    padding-left: 0;
    padding-right: 10px;
  }

  .pageSymbol {
    width: 13px;
    height: 13px;
    margin-right: 10px;
  }
}
