.container {
  padding: 0 20px;
}

.title {
  margin-top: 15vh;
  font-size: 44px;
  font-weight: 400;
  font-family: "Cyrvetica Extra Outline";
  color: rgba(255, 255, 255, 0.831);
  text-transform: uppercase;
  line-height: 1.1;
}

.subTitle {
  font-size: 21px;
  color: #ffffff;
  font-weight: bold;
  line-height: 1.122;
}

.cta {
  position: relative;
  display: inline-block;
  padding: 30px 50px;
  width: 228px;
  box-shadow: 0px 11px 18px 0px rgba(0, 1, 5, 0.28);
  background-color: rgb(107, 36, 64);
  border-radius: 40px;
  font-size: 14px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
}

.arrow {
  position: relative;
}

.arrow::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 5px;
  width: 30px;
  transform: translateY(-50%);
  height: 1px;
  background-color: #ffffff;
}

.arrow::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 5px;
  left: 30px;
  transform: rotate(45deg) translateY(-50%);
  transform-origin: top;
  height: 5px;
  border-top: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
}

