.container {
  padding: 0 20px;
}

.title {
  position: relative;
  margin-top: 15vh;
  padding-top: 10px;
  padding-left: 25px;
  font-size: 25px;
  color: #ffffff;
  font-weight: 800;
  line-height: 1.122;
}

.title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 22%;
  height: 1px;
  background-color: #d8771c;
}

@media only screen and (min-width: 768px) {
  .title {
    margin-left: 90px;
    padding-left: 40px;
    font-size: 41px;
  }
}

@media only screen and (min-width: 1024px) {
  .title::before {
    width: 120px;
  }
}
