.container {
  position: absolute;
  left: 16%;
  width: 84%;
  height: 100%;
}

.menu {
  display: grid;
  grid-template-columns: repeat(5, 20%);
  padding-left: 0;
  margin: 0;
  list-style: none;
  height: 100%;
}

.menuItem {
  width: 100%;
  text-align: center;
  padding-top: 70px;
  border-right: 1px solid #525252;
}

.link {
  color: inherit;
  text-decoration: none;
}

.active {
  border-top: 1px solid red;
}

@media only screen and (max-width: 768px) {
  .container {
    display: none;
  }
}
