.resultsContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 10vh;
  margin-bottom: 10vh;
  padding-left: 0;
  list-style: none;
}

.icon {
  margin-bottom: 10px;
  font-size: 30px;
}

.resultsItem:hover .icon {
  fill: url(#my-cool-gradient) #447799;
}

.resultsItem {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 15vh;
  padding: 10% 0;
  background-color: rgb(71, 71, 71);
  box-shadow: 0px 0px 49px 0px rgba(0, 1, 5, 0.1);
  color: #ffffff;
  font-size: 10px;
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.3s ease;
}

.resultsItem:hover {
  background-color: rgb(255, 255, 255);
  color: #323232;
}

@media only screen and (min-width: 768px) {
  .icon {
    margin-bottom: 15px;
    font-size: 40px;
  }

  .resultsItem {
    font-size: 14px;
  }
}
