.container {
  padding: 0 20px;
}

.title {
  position: relative;
  margin-top: 15vh;
  padding-top: 10px;
  padding-left: 25px;
  font-size: 24px;
  color: #ffffff;
  font-weight: 800;
  line-height: 1.122;
}

.title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 22%;
  height: 1px;
  background-color: #d8771c;
}

.stepsContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 25px;
  margin-top: 10vh;
  margin-bottom: 10vh;
  padding-left: 0;
  list-style: none;
  counter-reset: steps;
}

.step {
  position: relative;
  padding-top: 25px;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
}

.step::before {
  counter-increment: steps;
  content: counter(steps);
  position: absolute;
  top: 0;
  left: 3px;
  color: #d8771c;
}

@media only screen and (min-width: 768px) {
  .step {
    padding-top: 40px;
    font-size: 23px;
    line-height: 26px;
  }

  .stepsContainer {
    row-gap: 40px;
  }
}
